<template>
  <div class="box" :class="{ active: isActive }" @click="emitEvent">
    <div class="box__header">
      <div class="box__header-top">
        <div class="box__header-icon">{{ data.icon }}</div>
        <div v-if="data.count" class="box__header-counter">
          {{ data.count }}
        </div>
      </div>
      <div class="box__header-bottom">
        <h3 class="box__header-headline">{{ data.headline }}</h3>
        <span class="box__header-subhead">{{ data.subhead }}</span>
      </div>
    </div>
    <div
      ref="scrollable"
      class="box__content box__scrollable"
      @click="addClass"
    >
      <ul class="box__content-list">
        <li
          class="box__content-item"
          v-for="(singleItem, index) in tasks"
          :key="index"
        >
          <h3 class="box__content-title">{{ singleItem.title }}</h3>
          <span
            class="box__content-additional"
            v-for="(info, index) in singleItem.additional"
            :key="index"
          >
            {{ index == 0 ? info + " • " : info }}
          </span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Box",
  props: ["data", "tasks"],
  data() {
    return {
      isActive: false,
    };
  },
  watch: {
    tasks() {
      this.$refs.scrollable.scrollTop = 0;
    },
  },
  methods: {
    emitEvent() {
      this.$emit("event", this.data.event);
    },

    addClass() {
      if (this.data.event !== "add-class") return;
      this.isActive = !this.isActive;
    },
  },
};
</script>

<style scoped lang="scss">
@import "./scss/Box.scss";
</style>
