<template>
  <div id="app">
    <div class="container">
      <Box
        v-for="(boxData, index) in comBoxData"
        :key="index"
        :data="boxData"
        :tasks="boxData.tasks"
        :class="boxData.classes"
        @event="handleEvent"
      />
    </div>
    <Input v-if="inputVisible" @value="handleValue" @hide="hideInput" />
  </div>
</template>

<script>
import Box from "@/components/Box/Box.vue";
import Input from "@/components/Input/Input.vue";
import moment from "moment";

moment.locale("pl");

export default {
  name: "App",
  components: { Box, Input },
  computed: {
    comBoxData() {
      const tasks = this.tasks;
      const arr = [];
      const obj = [
        {
          icon: "A",
          count: 1,
          headline: "Ogólne",
          subhead: "Zobacz wszystkie zadania.",
        },
        {
          icon: "S",
          count: 3,
          headline: "Warto zobaczyć",
          subhead: "Zobacz wszystkie zadania.",
        },
        {
          icon: "B",
          headline: "Wszystkie",
          subhead: "Zobacz wszystkie zadania.",
          classes: "stretch horizontal",
          event: "add-class",
        },
        {
          icon: "D",
          headline: "Dzisiejsze",
          subhead: "Zobacz wszystkie zadania.",
          classes: "stretch vertical",
          event: "add-class",
        },
        {
          icon: "E",
          count: 1,
          headline: "Ważne",
          subhead: "Zobacz wszystkie zadania.",
        },
        {
          icon: "L",
          count: "0",
          headline: "Dodaj zadanie",
          subhead: "Dodaj kolejne zadanie.",
          event: "add-task",
        },
      ];

      tasks.forEach((element) => {
        const taskDate = moment(parseInt(element.timestamp + "000"));
        const currentDate = moment();

        const taskDayName = taskDate.format("dddd");
        const taskTime = taskDate.format("hh:mm A");
        element.additional = [taskDayName, taskTime];

        if (
          taskDate.format("D") === currentDate.format("D") &&
          taskDate.format("M") === currentDate.format("M") &&
          taskDate.format("Y") === currentDate.format("Y")
        ) {
          arr.push(element);
        }
      });
      obj[3].tasks = arr.reverse();
      obj[2].tasks = tasks.reverse();
      obj[2].count = tasks.length;
      return obj;
    },
  },
  data() {
    return {
      tasks: [],
      inputVisible: false,
    };
  },
  mounted() {
    if (localStorage.getItem("tasks")) {
      this.tasks = JSON.parse(localStorage.getItem("tasks"));
    }
  },
  methods: {
    handleValue(e) {
      let { value, date } = e;
      date = date !== undefined ? moment(date).unix() : moment().unix();
      const obj = {
        title: value,
        timestamp: date,
      };
      this.tasks.push(obj);
      localStorage.tasks = JSON.stringify(this.tasks);
      this.hideInput();
    },
    handleEvent(event) {
      switch (event) {
        case "add-task":
          this.showInput();
      }
    },
    showInput() {
      this.inputVisible = true;
    },
    hideInput() {
      this.inputVisible = false;
    },
  },
};
</script>

