<template>
  <div class="input" @keyup.enter="emitValue">
    <input type="text" name="" id="" v-model="value" />
    <input type="datetime-local" name="" id="" v-model="date" />
  </div>
</template>

<script>
export default {
  name: "Input",
  props: {},
  data() {
    return {
      value: "",
      date: undefined,
    };
  },
  mounted() {
    document.addEventListener("keyup", (evt) => {
      if (evt.keyCode === 27) {
        this.$emit("hide");
      }
    });
  },
  methods: {
    emitValue() {
      if (this.value.trim() === "") return;
      this.$emit("value", { value: this.value, date: this.date });
    },
  },
};
</script>

<style scoped lang="scss">
@import "./scss/Input.scss";
</style>
